.start-root {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.start-background {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    background: url(./../../resources/background.jpg) no-repeat center center fixed;
    background-size: cover;
}

.start-message {
    color: white;
    padding: 40px;
    font-size: 200%;
}

.start-selections {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    max-width: 900px;
    height: 20%;
}


